import axios from "axios";

class API {
  getPosts() {
    return axios
      .get(`/medium-like-app/` + `db.json`)
      .then(response => {
        return response.data.posts
      })
      .catch((error) => {
        throw error;
      });
  }

  getUser (email) {
    return axios.get(`/medium-like-app/` + `db.json`)
      .then(response => response.data.users.length > 0 ? response.data.users.find(user => user.email === email) : alert('User not found'))
      .catch( error => {throw error})
  }
}

export default new API();
